import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs';

import { Page } from '../page';

@Component({
  templateUrl: './quest-page.component.html'
})
export class QuestPageComponent implements OnInit {

  public pages: Map<number, Page> = new Map<number, Page>();

  public page: Page;

  public constructor(private http: HttpClient) {

  }

  public ngOnInit() {
    this.getJSON().subscribe((data: {pages: Page[]}) => {
      if (data.pages) {
        data.pages.forEach((page: Page) => this.pages.set(page.number, page));
      }
    });
  }

  public onClick(route: number): void {
    this.page = this.pages.get(route);
  }

  public getJSON(): Observable<any> {
    return this.http.get('quest.json');
  }

}

